import { cloneDeep } from 'lodash';
import * as fs from 'fs';
import * as gulp from 'gulp';
import * as merge from 'merge2';
import * as path from 'path';
import constants from './../constants';
import { writeStringToFile } from './../utilities/FileUtils';
import { EOL } from 'os';
import { ILocaleMap } from './../OdspGulpTask.ts';

import {
  IModuleConfig,
  IFrameworkModuleConfig,
  IInternalModuleConfig,
  ILocalizedInternalModuleConfig,
  ILocalizedString,
  IClientSideComponentLoaderConfig,
  IClientSideComponentManifest,
  IClientSideApplicationManifest,
  IClientSideWebPartManifest,
  IClientSideWebPartManifestEntry,
  IStandardLibraryModule,
  standardLibraryModules,
  IPath
} from '@microsoft/sp-module-interfaces';

import ManifestSchemaValidator,
       { IManifestValidatorResult } from '@microsoft/sp-module-interfaces/lib/manifestSchemaValidator';

import {
  IDeployEntry,
  IDeployModules
} from './../copyAssets/CopyAssetsTask';
import OdspGulpTask from './../OdspGulpTask';
import IsUrl from './../utilities/IsUrl';
import CumulativeManifestProcessor,
       { ICumulativeManifestOptions } from './cumulativeManifest/cumulativeManifestProcessor';

const schemaPropertyName: string = '$schema';

export interface IWriteManifestsTaskConfig {
  /**
   * The deploy CDN path.
   */
  deployCdnPath?: string;

  /**
   * A prefix to prepend to all assets' URLs. For example, if this value is "foobar", a resulting CDN path for an asset
   *  called "baz.js" will be "foobar/baz.js"
   */
  cdnUrlPrefix?: string;

  /**
   * The CDN base path. This value will populate the "internalModuleBaseUrls" CDN manifest property.
   */
  cdnBasePath?: string;

  /**
   * The debug base path, which is the value that will populate the "internalModuleBaseUrls" manifest property for
   *  debug manifests.
   */
  debugBasePath?: string;

  /**
   * If specified, only use this locale in the debug manifest.
   */
  debugLocale?: string;

  /**
   * Options for the cumulative manifest files to be dropped in the dist folder. If this field is null, cumulative
   *  files will not be produced.
   */
  cumulativeManifestOptions?: ICumulativeManifestOptions;

  /**
   * Defaults to true. If true, wrap the manifest in the Manifest Server format.
   * This should be true for all 1st party builds.
   */
  shouldWrapProductionManifestForManifestServer?: boolean;
}

interface ICdnManifest {
  ComponentType: string;
  Id: string;
  Manifest: string;
  Name: string;
  resources: {};
}

/**
 * Writes a manifest for each entry in the project.
 *
 * Example:
 *  IN:
 *   the result of the prepareDeploy step enumerating each entry, each entry's dependencies, and a developer-generated
 *    base manifest for each entry.
 *
 *  OUT:
 *   a debug and, optionally, a production manifest for each entry.
 */
export class WriteManifestsTask extends OdspGulpTask<IWriteManifestsTaskConfig> {
  public name: string = 'writemanifests';
  public taskConfig: IWriteManifestsTaskConfig = {
    deployCdnPath: undefined,
    debugLocale: constants.defaultLocale,
    cdnUrlPrefix: undefined,
    cdnBasePath: undefined,
    shouldWrapProductionManifestForManifestServer: false,

    cumulativeManifestOptions: {
      ignoreOutputManifestIds: undefined
    }
  };

  // tslint:disable-next-line:no-any
  private _localizedResourcesCache: { [key: string]: any } = {};

  /**
   * Normalizes a locale name. So "en-us" becomes "en-US" and "sr-cyrl-rs" becomes "sr-Cyrl-RS". There are two special
   *  cases. For pseudo-locales like "qps-ploc", everything stays lowecase. For language-script locales like "uz-Cyrl",
   *  only the script name's first character should be uppercased.
   */
  public normalizeLocaleName(localeName: string): string {
    localeName = localeName.toLowerCase();

    const localeTokens: string[] = localeName.split('-');
    if (localeTokens.length === 2) {
      const lastToken: string = localeTokens[1];
      if  (lastToken.length === 2) {
        localeTokens[1] = lastToken.toUpperCase();
      } else if (lastToken === 'ploc' || lastToken === 'plocm' || lastToken === 'ploca') {
        // Special case for pseudo-locales, leave lowercase
      } else {
        // The second token is the script token
        localeTokens[1] = this._uppercaseFirstLetter(lastToken);
      }
    } else if (localeTokens.length === 3) {
      const lastToken: string = localeTokens[2];
      if  (lastToken.length === 2) {
        localeTokens[2] = lastToken.toUpperCase();
        localeTokens[1] = this._uppercaseFirstLetter(localeTokens[1]);
      } else {
        // The last token is the script token
        localeTokens[2] = this._uppercaseFirstLetter(lastToken);
        localeTokens[1] = localeTokens[1].toUpperCase();
      }
    }

    const normalizedLocale: string = localeTokens.join('-');
    return normalizedLocale;
  }

  public executeTask(gulp: gulp.Gulp, completeCallback: (error?: string) => void): NodeJS.ReadWriteStream {
    if (!this.properties.deployEntries || this.properties.deployEntries.length === 0) {
      // Suppressed warning for now to unblock CI. VSO#219493
      this.log/*Warning*/('No deploy entries specified. No manifests to write.');
      completeCallback();
      return;
    }

    const shouldProduceDebugManifests: boolean = !!this.taskConfig.debugBasePath;
    if (!this.taskConfig.debugBasePath) {
      this.logWarning('Debug base path not specified. Unable to produce debug manifests.');
    }

    let shouldProduceProductionManifests: boolean = this.buildConfig.production && !!this.taskConfig.cdnBasePath;
    if (this.buildConfig.production && !this.taskConfig.cdnBasePath) {
      this.logWarning('CDN base path not specified. Unable to produce production manifests.');
    }

    if (this.buildConfig.production && !this.taskConfig.deployCdnPath) {
      shouldProduceProductionManifests = false;
      // Suppressed warning for now to unblock CI. VSO#219493
      this.log/*Warning*/('CDN deploy path not specified. Unable to produce production manifests.');
    }

    const debugManifests: IClientSideComponentManifest[] = [];
    const manifestIds: { [id: string]: IDeployEntry } = {};

    const tasks: NodeJS.ReadWriteStream[] = [];

    this.properties.deployEntries.forEach((deployEntry: IDeployEntry) => {
      if (!deployEntry.manifest) {
        this.logWarning(`Manifest not defined for entry "${deployEntry.entryName}".`);
        return;
      }

      const validationResult: IManifestValidatorResult =
        ManifestSchemaValidator.validateManifest(JSON.stringify(deployEntry.manifest));
      if (!validationResult.result) {
        const errorText: string = ManifestSchemaValidator.getFormattedErrorMessage(validationResult.errors);

        this.logError(`Manifest validation error (${deployEntry.bundleEntry.manifest}): ${EOL}${errorText}`);
        return;
      }

      // Ensure we don't have a duplicate ID
      const lowercaseId: string = deployEntry.manifest.id.toLowerCase();
      if (lowercaseId in manifestIds) {
        /* tslint:disable:max-line-length */
        this.logError(`Duplicate manifest ID "${lowercaseId}". Exists in "${manifestIds[lowercaseId].bundleEntry.manifest}" and "${deployEntry.bundleEntry.manifest}".`);
        /* tslint:enable:max-line-length */
      } else {
        manifestIds[lowercaseId] = deployEntry;
      }

      this._localizeManifest(deployEntry.manifest);

      if (shouldProduceDebugManifests) {
        const loaderConfig: IClientSideComponentLoaderConfig =
          this._generateLoaderConfig(deployEntry.debugModules,
                                     [this.taskConfig.debugBasePath],
                                     this._resolveDebugPath.bind(this),
                                     this.taskConfig.debugLocale);

        const debugManifest: IClientSideComponentManifest = this._generateManifest(deployEntry, loaderConfig);
        const isApplication: boolean = debugManifest.componentType === 'Application';

        if (isApplication) {
          const preloadModules: string[] = this._gatherPreloadManifestIds(deployEntry.debugModules);
          this._includePreloadManifestIds(preloadModules, debugManifest as IClientSideApplicationManifest);
        }

        debugManifests.push(debugManifest);

        tasks.push(writeStringToFile(JSON.stringify(debugManifest, undefined, 2),
                                     `${debugManifest.id}.manifest.json`)
                    .pipe(gulp.dest(this.buildConfig.distFolder)));
      }

      if (shouldProduceProductionManifests) {
        const loaderConfig: IClientSideComponentLoaderConfig =
          this._generateLoaderConfig(deployEntry.productionModules,
                                     [this.taskConfig.cdnBasePath],
                                     this._resolveProductionPath.bind(this));

        const productionManifest: IClientSideComponentManifest = this._generateManifest(deployEntry, loaderConfig);

        if (productionManifest.componentType === 'Application') {
          const preloadModules: string[] = this._gatherPreloadManifestIds(deployEntry.productionModules);
          this._includePreloadManifestIds(preloadModules, productionManifest as IClientSideApplicationManifest);
        }

        let cdnManifest: ICdnManifest | IClientSideComponentManifest;
        if (this.taskConfig.shouldWrapProductionManifestForManifestServer) {
          cdnManifest = {
            ComponentType: productionManifest.componentType,
            Id: productionManifest.id,
            Manifest: JSON.stringify(productionManifest),
            Name: productionManifest.id,
            resources: {}
          };
        } else {
          cdnManifest = productionManifest;
        }

        tasks.push(writeStringToFile(JSON.stringify(cdnManifest), `${productionManifest.id}.json`)
          .pipe(gulp.dest(this.taskConfig.deployCdnPath)));
      }
    });

    if (shouldProduceDebugManifests) {
      const cumulativeManifestOptions: ICumulativeManifestOptions = this.taskConfig.cumulativeManifestOptions;
      cumulativeManifestOptions.baseUrl = this.taskConfig.debugBasePath;

      const cumulativeManifestProcessor: CumulativeManifestProcessor =
        new CumulativeManifestProcessor(cumulativeManifestOptions, this);

      tasks.push(...cumulativeManifestProcessor.generateCumulativeManifest(gulp, debugManifests));
    }

    return merge(tasks).on('finish', () => completeCallback());
  }

  /**
   * Localizes a manifest. Only works with WebPart and Application manifests,
   * otherwise it does nothing
   */
  private _localizeManifest(manifest: IClientSideComponentManifest): void {
      switch (manifest.componentType) {
        case 'WebPart':
          // tslint:disable-next-line:no-any
          this._localizeWebPartManifest(manifest as IClientSideWebPartManifest<any>);
          break;

        case 'Application':
          this._localizeApplicationManifest(manifest as IClientSideApplicationManifest);
          break;
      }
  }

  /**
   * Localizes a WebPart manifest. That includes title, description and group
   * for all preconfigured entries
   */
  // tslint:disable-next-line:no-any
  private _localizeWebPartManifest(manifest: IClientSideWebPartManifest<any>): void {
    // tslint:disable-next-line:no-any
    manifest.preconfiguredEntries.forEach((entry: IClientSideWebPartManifestEntry<any>) => {
        entry.title = this._localizeString(entry.title);
        entry.description = this._localizeString(entry.description);
        if (entry.group) {
          entry.group = this._localizeString(entry.group);
        }
      }
    );
  }

  /**
   * Localizes an Application manifest. This includes title and description of the Application
   */
  private _localizeApplicationManifest(manifest: IClientSideApplicationManifest): void {
      manifest.title = this._localizeString(manifest.title);
      manifest.description = this._localizeString(manifest.description);
  }

  /**
   * Localizes a string with an id in the web $moduleName:expression;
   * If the string to localize is not based on an id, it returns the input param untouched.
   */
  private _localizeString(stringToLocalize: ILocalizedString): ILocalizedString {
    // If there is no id, return early
    if (!stringToLocalize.id) {
      return stringToLocalize;
    }

    const regex: RegExp = /^(?:\$)([^:]+)(?:\:)(.+)(?:\;)$/; // Test $<name>:<expr>;
    const matches: RegExpMatchArray = stringToLocalize.id.match(regex);

    if (matches && matches.length === 3) {
      this.logVerbose(`Localizing string '${matches[0]}'`);
      return this._localizeStringInternal(
        /* resourceName */ matches[1],
        /* expression */ matches[2],
        /* stringId */ matches[0]);
    } else {
      this.logError(`String id '${stringToLocalize.id}' has wrong format. It is not $<moduleName>:<expression>;`);
      // Return an empty ILocalizedString
      return {
      default: ''
      };
    }
  }

  /**
   * Localizes a string where it's been verified that the id follows the schema.
   * @param resourceName Name of the resource to load
   * @param expression Expression to be evaluated on the resource
   * @stringId Original id for the string. Used for logging purposes only
   * @returns Localized string with all found locales
   */
  private _localizeStringInternal(resourceName: string, expression: string, stringId: string): ILocalizedString {
    const localizedString: ILocalizedString = {
      default: ''
    };

    const resources: ILocaleMap = this.properties.discoveredLocalizedResources[resourceName];
    if (resources) {
      for (const locale in resources) {
        // tslint:disable-next-line:no-any
        const resource: any = this._getResource(resources[locale]);
        const normalizedLocaleName: string = this.normalizeLocaleName(locale);

        if (resource) {
          const evalString: string = expression.replace(resourceName, 'r');
          let stringFromExpression: string = undefined;

          try {
            // We support any format for the localized resource,
            // so we rely on the developer telling the code how to access it.
            // Eval generates risk. In order to minimize it, only one expression is allowed.
            // tslint:disable-next-line:no-eval
            stringFromExpression = eval(
              'function getLocalizedString(r) {' +
                'return ' + evalString +
              '}' +
              'getLocalizedString(resource)');
          } catch (error) {
            this.logWarning(`Problem extracting id '${stringId}' in locale '${normalizedLocaleName}'`);
          }

          if (stringFromExpression) {
            localizedString[normalizedLocaleName] = stringFromExpression;
          } else {
            this.logWarning(`Couldn't find id '${stringId}' in locale '${normalizedLocaleName}'`);
          }
        }
      }
    } else {
        this.logError(`Localized module '${resourceName}' not found`);
    }
    return localizedString;
  }

  /**
   * Gets a resource file from a path.
   */
  // tslint:disable-next-line:no-any
  private _getResource(resourcePath: string): any {
    if (!this._localizedResourcesCache[resourcePath]) {
      this._cacheResource(resourcePath);
    }
    return this._localizedResourcesCache[resourcePath];
  }

  /**
   * Caches a resource file from a path.
   * Because this execute in the build tools, function define is not defined,
   * so a fake function is created that will return the default element of the module.
   */
  /* tslint:disable:no-any */
  private _cacheResource(resourcePath: string): any {
    // Fake a define method to get the localizedResource module
    // tslint:disable-next-line:no-unused-variable
    function define(name: any, deps: any, callback: any): any {
      if (typeof name !== 'string') {
        callback = deps;
        deps = name;
        name = undefined;
      }

      if (!Array.isArray(deps)) {
        callback = deps;
        deps = undefined;
      }

      const exports: { default?: any } = {} ;
      const result: any = callback([undefined, exports]);
      return result.default || result || exports.default;
    }

    try {
      // We can't get the localized module dynamically at this point.
      // Reading and executing the module file in order to get the module
      const resourcesFile: string = fs.readFileSync(resourcePath, 'utf8');
      // tslint:disable-next-line:no-eval
      const resource: any = eval(resourcesFile);

      this._localizedResourcesCache[resourcePath] = resource;
    } catch (error) {
      this.logError(`Can't get resource '${resourcePath}'. ` +
        "Check that the module is correct and has doesn't require any external module.");
    }
  }
  /* tslint:enable:no-any */

  private _gatherPreloadManifestIds(deployModules: IDeployModules): string[] {
    const preloadIds: string[] = [];
    if (deployModules.frameworkModules) {
      deployModules.frameworkModules.forEach((_: IFrameworkModuleConfig, id: string) => {
        const standardLibraryModule: IStandardLibraryModule = standardLibraryModules[id];
        if (standardLibraryModule && standardLibraryModule.preloadId) {
          preloadIds.push(standardLibraryModule.preloadId);

          if (standardLibraryModule.dependentIds && standardLibraryModule.dependentIds.length) {
            preloadIds.push(...standardLibraryModule.dependentIds);
          }
        }
      });
    }

    return preloadIds;
  }

  private _includePreloadManifestIds(preloadIds: string[], manifest: IClientSideApplicationManifest): void {
    if (!manifest.preloadComponents) {
      manifest.preloadComponents = [];
    }

    for (const preloadId of preloadIds) {
      if (manifest.preloadComponents.indexOf(preloadId) === -1) {
        manifest.preloadComponents.push(preloadId);
      }
    }
  }

  private _generateManifest(deployEntry: IDeployEntry,
                            loaderConfig: IClientSideComponentLoaderConfig): IClientSideComponentManifest {
    const manifest: IClientSideComponentManifest = cloneDeep(deployEntry.manifest);
    delete manifest[schemaPropertyName];
    manifest.loaderConfig = loaderConfig;
    return manifest;
  }

  private _generateLoaderConfig(deployModules: IDeployModules,
                                internalModuleBaseUrls: string[],
                                resolvePath: (path: string | IPath) => string | IPath,
                                defaultLocaleOverride?: string): IClientSideComponentLoaderConfig {
    // Ensure all internalModuleBaseUrls have a trailing slash
    for (let i: number = 0; i < internalModuleBaseUrls.length; i++) {
      const internalModuleBaseUrl: string = internalModuleBaseUrls[i];
      if (!internalModuleBaseUrl.match(/\/$/)) {
        internalModuleBaseUrls[i] = `${internalModuleBaseUrl}/`;
      }
    }

    const scriptResources: { [name: string]: IModuleConfig } = {};

    if (deployModules.internalModules) {
      deployModules.internalModules.forEach((module: IInternalModuleConfig, id: string) => {
        // Clone the module so we can make changes to it
        module = cloneDeep(module);
        module.type = 'internal';
        scriptResources[id] = module;

        module.path = resolvePath(module.path);
      });
    }

    if (deployModules.localizedModules) {
      deployModules.localizedModules.forEach((module: ILocalizedInternalModuleConfig, id: string) => {
        // Clone the module so we can make changes to it
        module = cloneDeep(module);
        module.type = 'localized';
        scriptResources[id] = module;

        const oldPaths: { [locale: string]: string | IPath } = module.paths;
        module.paths = {};

        const lowercaseLocales: { [lowercaseLocale: string]: string } = {};
        for (const locale in oldPaths) {
          const path: string | IPath = oldPaths[locale];
          const lowercaseLocale: string = locale.toLowerCase();

          const normalizedLocale: string = this.normalizeLocaleName(lowercaseLocale);
          lowercaseLocales[lowercaseLocale] = normalizedLocale;

          module.paths[normalizedLocale] = resolvePath(path);
        }

        const lowercaseDefaultLocaleOverride: string = (defaultLocaleOverride && defaultLocaleOverride.toLowerCase)
                                                          ? defaultLocaleOverride.toLowerCase()
                                                          : undefined;
        if (lowercaseDefaultLocaleOverride && lowercaseDefaultLocaleOverride in lowercaseLocales) {
          module.defaultPath = module.paths[lowercaseLocales[defaultLocaleOverride]];
          delete module.paths;
        } else {
          module.defaultPath = resolvePath(module.defaultPath);
        }
      });
    }

    if (deployModules.frameworkModules) {
      deployModules.frameworkModules.forEach((module: IFrameworkModuleConfig, id: string) => {
        // Clone the module so we can make changes to it
        module = cloneDeep(module);
        module.type = 'framework';
        scriptResources[id] = module;

        if (module.failoverPath) {
          module.failoverPath = resolvePath(module.failoverPath);
        }
      });
    }

    if (!(deployModules.entryModuleId in scriptResources)) {
      this.logError(`Entry module "${deployModules.entryModuleId}" is missing a definition. The manifest will not ` +
                    'load');
    }

    const result: IClientSideComponentLoaderConfig = {
      entryModuleId: deployModules.entryModuleId,
      internalModuleBaseUrls: internalModuleBaseUrls,
      scriptResources: scriptResources
    };

    return result;
  }

  private _resolveDebugPath(assetPath: string | IPath): string | IPath {
    if (!assetPath) {
      return undefined;
    }

    if (typeof assetPath === 'string') {
      // If this is a full URL, don't change it
      if (IsUrl(assetPath)) {
        return assetPath;
      } else {
        return path.relative(this.buildConfig.rootPath, assetPath).replace(/\\/g, '/');
      }
    } else {
      return {
        default: this._resolveDebugPath(assetPath.default) as string,
        debug: this._resolveDebugPath(assetPath.debug) as string
      };
    }
  }

  private _resolveProductionPath(assetPath: string | IPath): string | IPath {
    if (!assetPath) {
      return undefined;
    }

    if (typeof assetPath === 'string') {
      let assetPathStr: string = assetPath as string;
      if (assetPathStr.charAt(0) === '/') {
        assetPathStr = assetPathStr.substr(1);
      }

      // If this is a full URL, don't change it
      if (IsUrl(assetPathStr)) {
        return assetPathStr;
      } else if (this.taskConfig.cdnUrlPrefix) {
        return path.posix.join(this.taskConfig.cdnUrlPrefix, assetPathStr);
      } else {
        return assetPathStr;
      }
    } else {
      return {
        default: this._resolveProductionPath(assetPath.default) as string,
        debug: this._resolveProductionPath(assetPath.debug) as string
      };
    }
  }

  private _uppercaseFirstLetter(str: string): string {
    return `${str.slice(0, 1).toUpperCase()}${str.slice(1)}`;
  }
}